.Login-form{
    position: absolute;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: url(../static/img/fon.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

}

.login{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 500px;
}

.login-background{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 640px;
    background-color: #ffffff;
    width: 770px;
    align-items: center;
    border-radius: 20px;
}

.login-h1{
    font-size: 20px;
    font-family: bold;
}

.line-login{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 275px;
}

.submit-login{
    height: 55px;
    background-color: #463ACB;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    border-radius: 8px;
    font-family: bold;
    border-width: 1px;
    border-style: solid;
    border-color: black;
    width: 630px;
}

.submit-login:hover{
    background-color: #A3A3A3;
}

.email-login{
    height: 45px;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    border-radius: 8px;
    font-family: bold;
    border-width: 1px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.100);
    text-align: justify;
    padding: 4px;
}

.password-login{
    height: 45px;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    border-radius: 8px;
    font-family: bold;
    border-width: 1px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.100);
    text-align: justify;
    padding: 4px;
}

.under-login{
    width: 630px;
    display: flex;
    justify-content: space-between;
}

.password-recovery{
    font-family: bold;
    color: black;
}

.register-login{
    font-family: bold;
    color: black;
}

